.App {
    font-family: 'Roboto', sans-serif;

}
.container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
/*    //grid-gap: 0.75rem; !* To define the gap between rows/columns *!
   // gap: 0.75rem;
    //padding: 0.75rem;*/
    background-color: #222629; /* For contrast */
    color: #fff;
}
/*.card {
    !* This will come in handy later to center the contents *!
    position: relative;
}*/

.card:after {
    content: "";
    display: block;
    padding-bottom: 100%;
}

.card .content {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.center {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 0.75rem;
    background-color: #474b4f;
}